import style from "./style.module.css";
import axios from "axios";
import { MdBusinessCenter } from "react-icons/md";
import React from "react";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function JobPostForm(props)
{
    const { isOpen, onRequestClose, contentLabel, job, setJob } = props;

    function handleChange(event)
    {
        setJob({
            ...job,
            [event.target.name]:event.target.value
        })
    }

    async function handleSubmit(event)
    {
        event.preventDefault();
        if(job.workMode==='online' && Object.hasOwn(job , 'location'))
        {
            delete job['location'];
        }
        try
        {
            const res = await axios.post("/api/postJob" , job ,
            {
                headers : {
                    'auth-token': JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                }
            });
            onRequestClose();
            toast.success(res.data);
        } catch (error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }

    return <>
        <ReactModal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            contentLabel={contentLabel}
            className={style.modalContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <form className={style.form} onSubmit={handleSubmit} >
                <div className={style.postHead}>Enter Job Details</div>
                <div className={style.inputContainer}>
                    <label htmlFor="title">Job Title</label>
                    <input type="text" name="title" id="title" placeholder="Enter job title" autoComplete='off' onChange={handleChange} required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="type">Job type</label>
                    <select id="type" name="jobType" onChange={handleChange} required>
                        <option value="fullTime">Full time</option>
                        <option value="partTime">Part time</option>
                    </select>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="description">Job description</label>
                    <textarea type="text" name="description" id="description" placeholder="Enter description" autoComplete='off' onChange={handleChange} required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="salary">Salary</label>
                    <input type="number" name="salary" id="salary" placeholder="Enter payment (in &#8377;)" autoComplete='off' onChange={handleChange} required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="experience">Past Experience</label>
                    <input type="number" name="experience" id="experience" placeholder="Past Experience required (years)" autoComplete='off' onChange={handleChange} required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="mode">Work mode</label>
                    <select id="mode" name="workMode" onChange={handleChange} required>
                        <option value="online">Online</option>
                        <option value="offline">Offline</option>
                        <option value="hybrid">Hybrid</option>
                    </select>
                </div>
                {job && job.workMode==="online" ? null : <div className={style.inputContainer}>
                    <label htmlFor="location">Location</label>
                    <input type="text" name="location" id="location" placeholder="Enter location" autoComplete='off' onChange={handleChange} required/>
                </div>}
                <div className={style.inputContainer}>
                    <label htmlFor="email">Email</label>
                    <input type="email" name="email" id="email" placeholder="Enter email to contact" autoComplete='off' onChange={handleChange} required/>
                </div>
                <div className={style.btnContainer}>
                    <button type='submit'>Post <MdBusinessCenter /></button>
                    <button onClick={props.onRequestClose}>Close</button>
                </div>
            </form>
        </ReactModal>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </>
};
