import List from "../../Components/List";
import Navbar from "../../Components/Navbar";
import style from "./style.module.css";
import React , { useState , useEffect , Suspense } from "react";
import axios from "axios";
import { useTheme } from '../../Context/ThemeContext';
import { Bars } from "react-loader-spinner";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Videos = React.lazy(()=> import("../../Components/Videos") )

export default function Home()
{
    const [userData, setuserData] = useState({_id:34 , username : "Baidurya"});
    const [search , manageSearch] = useState();
    const [searchVideo , setSearch] = useState();

    const { checked }=useTheme();

    async function getUser()
    {
        try
        {
            if(window.localStorage.getItem("auth-token")===null)
            return;
            const res=await axios.post("/api/getUser" , {} , { headers : {"auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]}});
            setuserData(res.data)
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }
    useEffect(()=>{
        getUser()
    },[])
    useEffect(()=>{
        console.log(userData);
    },[userData])
    return <div style={{backgroundImage : checked ? "linear-gradient(to right , #a0328a , #311bca)" : "linear-gradient(to right, #a8e6cf, #dcedc1)"}}>
        <Navbar userFirstCharecter={userData.username[0]} search={search} manageSearch={manageSearch} setSearch={setSearch} />
        <List />
        <div style={{paddingTop: "56px", minHeight: "100vh", display: "flex" , justifyContent : "right"}}>
            <Suspense fallback={<Bars
        height="80"
        width="80"
        color={checked ? "purple" : "#4fa94d"}
        ariaLabel="bars-loading"
        wrapperStyle={{}}
        wrapperClass={style.loader}
        visible={true}
        />}>
                <Videos search={searchVideo} />
            </Suspense>
        </div>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </div>
}