import style from './style.module.css'

import { FaUserCircle } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import { FaLock } from "react-icons/fa";
import { FaRegEye } from "react-icons/fa";
import { FaRegEyeSlash } from "react-icons/fa";

import React , { useState } from 'react';
import axios from 'axios';
import { useTheme } from '../../Context/ThemeContext';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Authentication() {
    const [action, manageAction] = useState(true);
    const [data, manageData] = useState({});
    const [passwordVisibility , setPasswordVisibility] = useState(false);

    const { checked } = useTheme();

    function HandleClick()
    {
        manageAction(!action)
        const container = document.getElementById('container');
        action ? container.classList.add(style.active) : container.classList.remove(style.active);
    }
    function handleChange(event)
    {
        manageData({
            ...data,
            [event.target.name]:event.target.value
        })
    }
    const validateEmail = (email) => {
        const re = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/;

        if (re.test(email)) {
            return true;
        } else {
            return false;
        }
    };

    const validatePassword = (password) => {
        const isPassValid = password.match(/[a-z]/g) && password.match(/[A-Z]/g) && password.match(/[0-9]/g) && password.match(/[^a-zA-Z\d]/g) && password.length >= 6;

        return isPassValid;
    };
    async function handleSubmit(event)
    {
        event.preventDefault();
        if(!action)
        {
            data["createdOn"] = new Date();
        }
        if(!validateEmail(data.email))
        {
            toast.error("Invalid email");
            return;
        }
        if(!validatePassword(data.password))
        {
            toast.error("Password must contain atleast 1 each lowercase, uppercase, special character, digit and must be 6 characters long");
            return;
        }
        try
        {
            const res=await axios.post(`/api/${action===true ? 'signin' : 'signup'}` , data);
            window.localStorage.setItem("auth-token" , JSON.stringify(res.data));
            window.location.href="/";
            toast.success(`Signed ${action===true ? 'In' : 'Up'} Successfully`);
        } catch (error) {
            console.log(error);
            toast.warn(error.response.data);
        }
    }
    return (
        <div className={style.body}>
            <div className={style.container} id="container">
                <div className={`${style.formContainer} ${style.signup}`}>
                    <form onSubmit={handleSubmit}>
                        <div className={style.header}>Create Account</div>
                        <div className={style.inputHolder}>
                            <FaUserCircle size={20} color='black'/>
                            <input type="text" name='username' placeholder="Username" onChange={handleChange} autoComplete='off' required />
                        </div>
                        <div className={style.inputHolder}>
                            <MdEmail size={20} color='black'/>
                            <input type="email" name="email" placeholder="Email" onChange={handleChange} autoComplete='off' required />
                        </div>
                        <div className={style.inputHolder}>
                            <FaLock size={20} color='black'/>
                            <input type={passwordVisibility ? "text" : "password"} name="password" placeholder="Password" onChange={handleChange} autoComplete='off' required />
                            {passwordVisibility ? <FaRegEye style={{cursor : "pointer"}} onClick={()=>setPasswordVisibility(!passwordVisibility)} size={20} /> : 
                            <FaRegEyeSlash style={{cursor : "pointer"}} onClick={()=>setPasswordVisibility(!passwordVisibility)} size={20} />}
                        </div>
                        <button type='submit' style={{backgroundColor : checked ? "#512da8" : "rgb(2 217 116)"}}>Sign Up</button>
                    </form>
                </div>
                <div className={`${style.formContainer} ${style.signin}`}>
                    <form action='submit' onSubmit={handleSubmit}>
                        <div className={style.header}>Sign In</div>
                        <div className={style.inputHolder}>
                            <MdEmail size={20} color='black'/>
                            <input type="email" name="email" placeholder="Email" onChange={handleChange} autoComplete='off' required/>
                        </div>
                        <div className={style.inputHolder}>
                            <FaLock size={20} color='black'/>
                            <input type={passwordVisibility ? "text" : "password"} name="password" placeholder="Password" onChange={handleChange} autoComplete='off' required/>
                            {passwordVisibility ? <FaRegEye style={{cursor : "pointer"}} onClick={()=>setPasswordVisibility(!passwordVisibility)} size={20} /> :
                            <FaRegEyeSlash style={{cursor : "pointer"}} onClick={()=>setPasswordVisibility(!passwordVisibility)} size={20} />}
                        </div>
                        <button type='submit' style={{backgroundColor : checked ? "#512da8" : "rgb(2 217 116)"}}>Sign In</button>
                    </form>
                </div>
                <div className={style.toggleContainer}>
                    <div className={style.toggle} style={{backgroundColor : checked ? "#512da8" : "rgb(2 217 116)" , color : checked ? "white" : "darkgreen"}}>
                        <div className={`${style.togglePanel} ${style.toggleLeft}`} style={{background : checked ? "linear-gradient(to right, #5c6bc0, #512da8)" : "linear-gradient(to right, rgb(107 255 148), rgb(1 239 127))"}}>
                            <h1>Hello, Friend!</h1>
                            <p>Register with your personal details to use all of site features</p>
                            <button className={style.hidden} id="login" onClick={HandleClick} style={{color : checked ? "white" : "darkgreen" , borderColor : checked ? "white" : "darkgreen"}}>Sign In</button>
                        </div>
                        <div className={`${style.togglePanel} ${style.toggleRight}`} style={{background : checked ? "linear-gradient(to right, #5c6bc0, #512da8)" : "linear-gradient(to right, rgb(107 255 148), rgb(1 239 127))"}}>
                            <h1>Welcome Back!</h1>
                            <p>Enter your personal details to use all of site features</p>
                            <button className={style.hidden} id="register" onClick={HandleClick} style={{color : checked ? "white" : "darkgreen" , borderColor : checked ? "white" : "darkgreen"}}>Sign Up</button>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
        </div>
    );
}