import style from "./style.module.css";
import { MdUploadFile } from "react-icons/md";
import React from "react";
import { useTheme } from "../../Context/ThemeContext";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Upload()
{
    const { checked } = useTheme();

    async function handleSubmit(event)
    {
        event.preventDefault();
        const form = event.target;
        const formData = new FormData(form);
        const uploadingVideo = {
            method: 'POST',
            headers: {
              'auth-token': JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
            },
            body: formData,
        };
        try {
            const res = await fetch('/api/upload', uploadingVideo);

        if (res.ok) {
            console.log('Success!');
            toast.success(res.data);
        } else {
            console.error('Error:', res.statusText);
        }
        } catch (error) {
            console.error('Error:', error);
            toast.warn("Internal Server Error");
        }
        finally
        {
            console.log("Done");
            window.history.back();
        }
    }

    return <div className={style.body}>
        <div className={style.main}>
            <form className={style.form} method="POST" action="/api/upload" encType="multipart/form-data" onSubmit={handleSubmit}>
                <div style={{color : "black"}}>Upload Details <MdUploadFile /></div>
                <div>
                    <label>Upload Video</label>
                    <input type="file" name="video" accept="video/*" required style={{color : "black"}}/>
                    <label>Thumbnail</label>
                    <input type="file" name="image" accept="image/*" alt="Image" required style={{color : "black"}}/>
                    <label>Title</label>
                    <textarea name="title" placeholder="Enter the title" required />
                    <label>Description</label>
                    <textarea name="description" placeholder="Enter the description" />
                </div>
                <div>
                    <button type="submit" style={{backgroundColor : checked ? "#512da8" : "rgb(2 217 116)"}}>Upload</button>
                </div>
            </form>
            <div className={style.text} style={{background : checked ? "linear-gradient(to right, #5c6bc0, #512da8)" : "linear-gradient(to right, rgb(107 255 148), rgb(1 239 127))" , color : checked ? "white" : "darkgreen"}}>
                <div>Upload the Video and Thumbnail</div>
                <div>Don't forget to add the title and description !</div>
            </div>
        </div>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </div>
}