import style from "./style.module.css";
import { BiSolidLike } from "react-icons/bi";
import { MdHistory , MdWatchLater , MdOutlineArrowBackIos , MdOutlineArrowForwardIos } from "react-icons/md";
import { BsPersonWorkspace } from "react-icons/bs";
import { GrChannel } from "react-icons/gr";
import { FaChevronRight } from "react-icons/fa";
import { IoMdLogOut } from "react-icons/io";
import { useTheme } from '../../Context/ThemeContext';

import axios from "axios";
import { Link } from "react-router-dom";
import React , { useEffect, useState } from "react";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const com = [
    { title: "Liked Videos", icon: BiSolidLike, link: window.localStorage.getItem("auth-token")!==null ? "/LikedVideos" : "/"},
    { title: "Watch Later", icon: MdWatchLater, link: window.localStorage.getItem("auth-token")!==null ? "/WatchLater" : "/" },
    { title: "History", icon: MdHistory, link: window.localStorage.getItem("auth-token")!==null ? "/History" : "/" },
    { title: "Job Portal", icon: BsPersonWorkspace, link: window.localStorage.getItem("auth-token")!==null ? "/JobPortal" : "/" }]
    export default function List()
    {
        const [channels , setChannels]=useState([]);
        const [toggle , settoggle]=useState(true);
        const [slideNav , setslideNav]=useState(window.innerWidth>1040 ? false : true);
        const { checked } = useTheme();
        useEffect(()=>{
            async function getSubscribedChannels()
            {
                try
                {
                    if(window.localStorage.getItem("auth-token")===null)
                    return;
                    const res=await axios.get("/api/getSubscribedChannels" ,
                    {
                        headers : {
                            'auth-token': JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                        }
                    })
                    setChannels(res.data);
                }
                catch(error)
                {
                    console.log(error);
                }
            }
            getSubscribedChannels();
        },[])

        function handleToggle()
        {
            settoggle(!toggle);
        }

        function slideSideNav()
        {
            setslideNav(!slideNav);
        }

        function checkLoggedIn()
        {
            if(window.localStorage.getItem("auth-token")===null)
            {
                toast.info("oops ! You need to log in first !")
            }
            return;
        }

        function logout()
        {
            if(window.localStorage.getItem("auth-token")!==null)
            {
                window.localStorage.clear();
                window.location.reload();
            }
        }

        function Component(props) {
            return <Link onClick={checkLoggedIn} to={props.link} className={style.component} style={{color : checked ? "white" : "#b90bb2"}}><props.icon size={23} className={style.icons}/><span>{props.title}</span></Link>
        }
        return <><div className={style.body} style={{scrollbarColor : "blue", width : (slideNav ? '0px' : '274px'), backgroundColor : (window.innerWidth>1040 ? null : (checked ? "#a0328a" : "#a8e6cf"))}}>
            {com.map((item , index) => {
                return <Component key={index} title={item.title} icon={item.icon} link={item.link}/>
            })}
            <div className={style.component} onClick={handleToggle} style={{color : checked ? "white" : "#b90bb2"}}>
                <GrChannel size={23} className={style.icons} />
                <span>Subscribed Channels</span>
                <FaChevronRight style={{transform : `rotateZ(${toggle ? 90 : 0}deg)` , marginLeft : "5px"}} />
            </div>
            <div style={{display : toggle ? 'flex' : 'none'}} className={style.channelsContainer}>
                {channels.map((item , index)=>{
                    return <div className={style.channels} key={index} style={{color : checked ? "white" : "#b90bb2"}}>{item}</div>
                })}
            </div>
            <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
            {window.localStorage.getItem("auth-token") ? <div className={style.component} onClick={logout} style={{color : checked ? "white" : "#b90bb2"}}>
                <IoMdLogOut size={23} className={style.icons} />
                <span>Log Out</span>
            </div> : null}
        </div>
            <button className={style.sideArrow} onClick={slideSideNav}
            style={{left: (slideNav ? '0px' : (window.innerWidth>512 ? '279px' : '200px')) ,
            backgroundColor : checked ? "#a0328a" : "#a8e6cf" ,
            color : checked ? "#311bca" : "#dcedc1"}}
            >{slideNav ? <MdOutlineArrowForwardIos size={27} /> : <MdOutlineArrowBackIos size={27} />}
            </button>
        </>
}