import React from "react";
import { useNavigate } from "react-router-dom";
import style from "./style.module.css";

import { MdOutlineArrowBackIos } from "react-icons/md";

export default function PageNotFound()
{
    const history = useNavigate();
    return <div className={style.body}>
        <div className={style.text}>
            <div className={style.notFound}>Looks like the page you are looking for is no longer available</div>
            <button className={style.goBack} onClick={()=>history(-1)}><MdOutlineArrowBackIos />Go Back</button>
            <button className={style.returnHome} onClick={()=>history("/")}>Return to Home Screen</button>
        </div>
        <div className={style.imageContainer}>
            <img className={style.image} src="/images/PageNotFound.jpg" alt="image not found" />
        </div>
    </div>
}