import axios from "axios";
import React , { useState , useEffect } from "react";
import { Link } from "react-router-dom";
import style from "./style.module.css";
import { IoMdShare , IoMdRemoveCircleOutline } from "react-icons/io";
import Share from "../../Components/Share/page";
import { useTheme } from "../../Context/ThemeContext";
import { Bars } from "react-loader-spinner";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Choice(props)
{
    const [videos , manageVideos] = useState();
    const [shareModal , setShareModal] = useState(false);
    const [videoLink , setVideoLink] = useState(null);
    const { checked } = useTheme();

    function openModal(id)
    {
        setVideoLink(`http://localhost:3000/Videos/${id}`);
        setShareModal(true);
        document.body.style.overflow = 'hidden';
    }

    function closeModal()
    {
        setShareModal(false);
        setVideoLink(null);
        document.body.style.overflow = 'auto';
    }

    const page = {
        likedVideos : {
            name : "Liked Videos",
            url : "/api/allLikedVideos",
            removeUrl : "/api/removeLikedVideos"
        },
        watchLater : {
            name : "Watch Later",
            url : "/api/getWatchLater",
            removeUrl : "/api/manageWatchLater"
        },
        history : {
            name : "Your History",
            url : "/api/getHistory",
            removeUrl : "/api/removeHistory"
        }
    }

    async function remove(param)
    {
        const [name , id] = param;
        try
        {
            const res = await axios.patch(page[name].removeUrl ,
            {videoId : id},
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                    "action" : false
                }
            })
            manageVideos(video => (video.filter((item)=>{
                return item.id!==id
            })))
            toast.success(res.data);
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }

    useEffect(()=>{
        async function getAllLikedVideos()
        {
            const res = await axios.get(page[props.name].url ,
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]
                }
            })
            manageVideos(res.data);
        };
        getAllLikedVideos()
    },[])
    return <div>
        <div className={style.header} style={{color : checked ? "white" : "#04ad0d"}}>{page[props.name].name}</div>
        <div className={style.videos}>
        {!videos ? <Bars
        height="80"
        width="80"
        color={checked ? "purple" : "#4fa94d"}
        ariaLabel="bars-loading"
        wrapperStyle={{}}
        wrapperClass={style.loader}
        visible={true}
        /> : <>{videos.length>0 ? <>
            {videos.map(item => (
                <div className={style.item} key={item.id}>
                    <Link className={style.videoDiv} style={{color : checked ? "white" : "black" , borderRight : checked ? "2px solid rgb(174, 173, 173)" : "2px solid #04ad0d"}} to={`/Videos/${item.id}`}>
                        <img className={style.thumbnail} src={item.imageData} alt=""/>
                        <div>
                            <div className={style.title}>{item.title}</div>
                            <div className={style.creator}>{item.username}</div>
                        </div>
                    </Link>
                    <div>
                        <span onClick={()=>openModal(item.id)} style={{color : checked ? "white" : "black" , borderBottom : checked ? "1px solid rgb(174, 173, 173)" : "1px solid #04ad0d"}}><IoMdShare />Share</span>
                        <span onClick={()=>remove([props.name , item.id])} style={{color : checked ? "white" : "black" , borderBottom : checked ? "1px solid rgb(174, 173, 173)" : "1px solid #04ad0d"}}><IoMdRemoveCircleOutline />Remove</span>
                    </div>
                </div>
            ))}</> : <div className={style.empty} style={{color : checked ? "white" : "#04ad0d"}}>------ No Videos Available ------</div>}</>}
        </div>
        <Share isOpen={shareModal} onRequestClose={closeModal} contentLabel="Example Modal" link={videoLink} />
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </div>;
};
