import React from "react";
import style from "./style.module.css";

import { FacebookIcon , FacebookShareButton , WhatsappIcon , WhatsappShareButton , EmailIcon , EmailShareButton , TelegramIcon , TelegramShareButton , LinkedinIcon , LinkedinShareButton , TwitterIcon , TwitterShareButton } from "react-share";

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function Share(props)
{
    const { isOpen , onRequestClose , contentLabel , link } = props;

    function ShareOptions(props)
    {
        return <div className={style.icon}>
            <props.buttonName url={link ? link : window.location.href}>
                <props.iconName round={true} />
            </props.buttonName>
            <div className={style.name}>{props.name}</div>
        </div>
    }

    return <>
        <ReactModal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            contentLabel={contentLabel}
            className={style.modalContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <div className={style.header}>Share To...</div>
            <div className={style.body}>
                <ShareOptions name="FaceBook" buttonName={FacebookShareButton} iconName={FacebookIcon} />
                <ShareOptions name="WhatsApp" buttonName={WhatsappShareButton} iconName={WhatsappIcon} />
                <ShareOptions name="Email" buttonName={EmailShareButton} iconName={EmailIcon} />
                <ShareOptions name="Telegram" buttonName={TelegramShareButton} iconName={TelegramIcon} />
                <ShareOptions name="Linkedin" buttonName={LinkedinShareButton} iconName={LinkedinIcon} />
                <ShareOptions name="Twitter" buttonName={TwitterShareButton} iconName={TwitterIcon} />
            </div>
        </ReactModal>
    </>
}