import "./style.css"
import { useTheme } from '../../Context/ThemeContext';

import { PiVideoBold } from "react-icons/pi";
import { FaSearch , FaUserAlt } from "react-icons/fa";
import { RiVideoUploadFill } from "react-icons/ri";
import { MdOutlineArrowBackIos } from "react-icons/md";

import { Link } from "react-router-dom";
import React , { useState } from "react";
export default function Navbar(props)
{
    const { toggleColor , checked } = useTheme();
    const [toggle , handleToggle] = useState(false);
    const [secondNav , setSecondNav] = useState(false);
    const ob1 = {
        opacity : "0" ,
        pointerEvents : "none",
        transition: "opacity 0.3s ease-in-out , transform 0.3s ease-in-out",
        transform: "translateY(-10px)"
    }
    const ob2 = {
        opacity : "1" ,
        pointerEvents : "auto",
        transform: "translateY(0px)"
    }
    function logout()
    {
        if(window.localStorage.getItem("auth-token")!==null)
        {
            window.localStorage.clear();
            window.location.reload();
        }
    }
    function Links(props)
    {
        return <div className="dropdown-menu">
            <Link to={props.link} style={{display : "contents"}}>
                <button style={{height : props.height}} className="navBtn"><props.iconName size={24} color={checked ? "white" : "black"} /></button>
                <div className="dropdown-content">{props.dropdownContent}</div>
            </Link>
        </div>
    }
    function handleSecondNav()
    {
        setSecondNav(!secondNav);
    }
    function manageSearch(event)
    {
        props.manageSearch(event.target.value);
    }
    function search()
    {
        props.setSearch(props.search);
    }
    return <div id="Navbar" style={{color : checked ? "white" : "rgb(12 109 122)"}}>
            {!secondNav ? <>
            <div className="navComponents">
                <PiVideoBold size={36} className="webIcon"/>
                <span>VidTube</span>
            </div>
            <div className="navComponents">
                <input id="inputNav" type="text" placeholder="Serach for Videos" value={props.search} onChange={manageSearch} />
                <button className="search" style={{backgroundColor : checked ? "#56b5ff" : "lightgreen"}} onClick={search}><FaSearch size={27}/></button>
            </div>
            <div className="navComponents" style={{justifyContent: "right" , marginLeft : "auto"}}>
                <button className="secondSearch" style={{color : checked ? "white" : "black"}}><FaSearch size={27} onClick={handleSecondNav} /></button>
                <Links iconName={RiVideoUploadFill} link={window.localStorage.getItem("auth-token")===null ? "./Authentication" : "./UploadVideo"} height="80%" dropdownContent="Upload Video" />
                {window.localStorage.getItem("auth-token")!==null?
                    <div className="dropdown-menu">
                        <button id="userFirstCharecter" onClick={()=>{handleToggle(!toggle)}}>{props.userFirstCharecter}</button>
                        <ul id="accountOptions" style={!toggle ? ob1 : ob2}>
                            <li><Link to={"/YourChannel"} style={{color : "black"}}><span>Your Channel</span></Link></li>
                            <li onClick={logout}>Logout</li>
                        </ul>
                    </div> : 
                    <Links iconName={FaUserAlt} link={"./Authentication"} height="80%" dropdownContent="Log in/Sign Up"/>
                }
                <div className="toggle-slider">
                    <input
                        type="checkbox"
                        id="toggle"
                        checked={checked}
                        onChange={toggleColor}
                    />
                    <label htmlFor="toggle" className="slider" />
                </div>
            </div></> :
            <div className="secondNav">
                <button className="back"><MdOutlineArrowBackIos onClick={handleSecondNav} size={27} /></button>
                <input id="jobSearch" className="inputNav2" type="text" placeholder="serach for Videos" value={props.search} onChange={manageSearch} />
                <button className="search" style={{backgroundColor : checked ? "#56b5ff" : "lightgreen"}} onClick={search}><FaSearch size={27}/></button>
            </div>}
        </div>
}