import style from "./style.module.css";
import { FaSearch } from "react-icons/fa";
import React , { useState , Suspense } from "react";
import { useTheme } from "../../Context/ThemeContext";

import { MdOutlineArrowBackIos , MdOutlineWork } from "react-icons/md";
import { IoFilterSharp } from "react-icons/io5";
import { Bars } from "react-loader-spinner";

import JobPostForm from "../../Components/JobPostForm/page";
import JobFilters from "../../Components/JobFilters/page";
const AvailableJobs = React.lazy(() => import("../../Components/AvailableJobs/page"));
const AppliedJobs = React.lazy(() => import("../../Components/AppliedJobs/page"));
const PostedJobs = React.lazy(() => import("../../Components/PostedJobs/page"));

export default function Jobs()
{
    const [modalIsOpen, setIsOpen] = useState(false);
    const [filter, setFilter] = useState(false);
    const [job  , setJob] = useState({'jobType' : 'fullTime' , 'workMode' : 'online'})
    const [option , setOption] = useState("AvailableJobs")
    const [filters  , setFilters] = useState(null);
    const [newFilters  , setNewFilters] = useState();
    const [searchItem  , setSearchItem] = useState();
    const [respSearch  , setrespSearch] = useState(true);

    const { checked } = useTheme();

    function openModal()
    {
        setIsOpen(true);
        document.body.style.overflow = 'hidden';
    }

    function closeModal()
    {
        setIsOpen(false);
        setJob({'jobType' : 'fullTime' , 'workMode' : 'online'});
        document.body.style.overflow = 'auto';
    }

    function openFilter() {
        setFilter(true);
        document.body.style.overflow = 'hidden';
    }

    function closeFilter()
    {
        setFilter(false);
        document.body.style.overflow = 'auto';
    }

    function handleChange(event)
    {
        setOption(event.target.value);
    }

    function handleFilter(event)
    {
        setFilters({
            ...filters,
            [event.target.name]:event.target.value
        })
    }

    async function applyFilter(event)
    {
        event.preventDefault();
        setNewFilters(filters);
        closeFilter();
        setFilters(null);
    }

    function search()
    {
        const search=document.getElementById("jobSearch").value;
        setSearchItem(search);
    }

    function search2()
    {
        setrespSearch(!respSearch);
    }

    return <div className={style.body}>
        <div className={style.header}>
            {respSearch ? <><div className={style.inputContainer}>
                <input id="jobSearch" className={style.input} type="text" placeholder="serach for Creators , Posts or Places...." />
                <button className={style.search} onClick={search}><FaSearch size={30}/></button>
            </div>
            <div>
                <button className={style.search2} onClick={search2}><FaSearch size={30}/></button>
            </div>
            <div className={style.dropdownMenu}>
                <button style={{height : "80%"}} className={style.navBtn} onClick={openFilter}><IoFilterSharp size={24} color={checked ? "white" : "#04ad0d"} /></button>
                <div className={style.dropdownContent}>Filter</div>
            </div>
            <div className={style.dropdownMenu}>
                <button style={{height : "80%"}} className={`${style.navBtn} ${style.work}`} onClick={openModal}><MdOutlineWork size={30} color="brown" /></button>
                <div className={style.dropdownContent}>Post Job</div>
            </div>
            <select onChange={handleChange}>
                <option value="AvailableJobs">Available Jobs</option>
                <option value="AppliedJobs">Applied Jobs</option>
                <option value="PostedJobs">Posted Jobs</option>
            </select>
            </> :
            <div className={style.newInputContainer}>
                <button className={style.back}><MdOutlineArrowBackIos onClick={search2} size={30} /></button>
                <input id="jobSearch" className={style.input2} type="text" placeholder="serach for Creators , Posts or Places...." />
                <button className={style.search} onClick={search}><FaSearch size={30}/></button>
            </div>}
        </div>
        <Suspense fallback={<Bars
        height="80"
        width="80"
        color={checked ? "purple" : "#4fa94d"}
        ariaLabel="bars-loading"
        wrapperStyle={{}}
        wrapperClass={style.loader}
        visible={true}
        />}>
        {option === "AvailableJobs" && <AvailableJobs newFilters={newFilters} search={searchItem} />}
        {option === "AppliedJobs" && <AppliedJobs newFilters={newFilters} search={searchItem} />}
        {option === "PostedJobs" && <PostedJobs newFilters={newFilters} search={searchItem} />}</Suspense>
        <JobPostForm isOpen={modalIsOpen} onRequestClose={closeModal} contentLabel="Example Modal" job={job} setJob={setJob}/>
        <JobFilters isOpen={filter} onRequestClose={closeFilter} contentLabel="Example Modal" filters={filters} handleFilter={handleFilter} applyFilter={applyFilter} />
    </div>
};
