import React from "react";
import { BrowserRouter as Router, Route, Routes, Navigate } from "react-router-dom";
import Home from "./Pages/Home/page.jsx";
import Authentication from "./Pages/Authentication/page.jsx";
import Upload from "./Pages/Upload/page.jsx";
import VideoContent from "./Pages/Video Content/page.jsx";
import Choice from "./Pages/Choice/page.jsx";
import Channel from "./Pages/Channel/page.jsx";
import Jobs from "./Pages/Jobs/page.jsx";
import "./global.css";
import { ThemeProvider, useTheme } from "./Context/ThemeContext.jsx";
import PageNotFound from "./Pages/PageNotFound/page.jsx";

const App = () => {
  return (
    <ThemeProvider>
      <MainApp />
    </ThemeProvider>
  );
};

const MainApp = () => {
  const { checked } = useTheme();

  function checkLoggedIn()
  {
      if(window.localStorage.getItem("auth-token")===null)
      {
          return false;
      }
      else
      {
        return true;
      }
  }

  return (
    <div
      style={{
        backgroundImage: checked
          ? "linear-gradient(to right , #a0328a , #311bca)"
          : "linear-gradient(to right, #a8e6cf, #dcedc1)",
        minHeight: "100vh",
        color: checked ? "white" : "black"
      }}
    >
      <Router>
        <Routes>
          <Route exact path="/" element={<Home />} />
          <Route exact path="/Authentication" element={!checkLoggedIn() ? <Authentication /> : <Navigate to="/" replace />} />
          <Route exact path="/UploadVideo" element={checkLoggedIn() ? <Upload /> : <Navigate to="/" replace />} />
          <Route exact path="/Videos/:id" element={<VideoContent />} />
          <Route exact path="/LikedVideos" element={checkLoggedIn() ? <Choice name="likedVideos" /> : <Navigate to="/" replace />} />
          <Route exact path="/WatchLater" element={checkLoggedIn() ? <Choice name="watchLater" /> : <Navigate to="/" replace />} />
          <Route exact path="/History" element={checkLoggedIn() ? <Choice name="history" /> : <Navigate to="/" replace />} />
          <Route exact path="/YourChannel" element={checkLoggedIn() ? <Channel /> : <Navigate to="/" replace />} />
          <Route exact path="/JobPortal" element={checkLoggedIn() ? <Jobs /> : <Navigate to="/" replace />} />
          <Route exact path="*" element={<PageNotFound />} />
        </Routes>
      </Router>
    </div>
  );
};

export default App;