import React , { useState , useEffect } from "react";
import style from "./style.module.css";
import axios from "axios";
import { useTheme } from "../../Context/ThemeContext";

import { TiTick } from "react-icons/ti";
import { Link } from "react-router-dom";
import { MdDelete } from "react-icons/md";
import { IoMdShare } from "react-icons/io";
import { HiPencilSquare } from "react-icons/hi2";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Share from "../../Components/Share/page";

export default function Channel()
{
    const [video , setVideo] = useState({});
    const [description , setDescription] = useState('');
    const [shareModal , setShareModal] = useState(false);
    const [videoLink , setVideoLink] = useState(null);
    const { checked } = useTheme();

    function openModal(id)
    {
        setVideoLink(`http://localhost:3000/Videos/${id}`);
        setShareModal(true);
        document.body.style.overflow = 'hidden';
    }

    function closeModal()
    {
        setShareModal(false);
        setVideoLink(null);
        document.body.style.overflow = 'auto';
    }

    useEffect(()=>{
        async function fetchData()
        {
            try
            {
                const res = await axios.get("/api/myVideos" , 
                {
                    headers : {
                        "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]
                    }
                })
                setVideo(res.data);
                setDescription(res.data.description);
            }
            catch(error)
            {
                console.log(error);
                toast.warn(error.response.data);
            }
        }
        fetchData();
    },[])
    async function updateProfile(data)
    {
        try
        {
            const res = await axios.patch("/api/updateProfile" , data ,
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]
                }
            })
            toast.success(res.data);
            window.location.reload();
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }
    async function deleteVideo(id)
    {
        try
        {
            const res = await axios.delete(`/api/deleteVideo/${id}` ,
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]
                }
            });
            setVideo(video => ({...video , videos : video.videos.filter((item)=>{
                return item.id!==id
            })}))
            toast.success(res.data);
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }
    return <div className={style.body}>
        <div className={style.header}>
            <div className={style.userData}>
                <div className={style.logo} style={{background : checked ? "linear-gradient(to right, #a9b2e4, #512da8)" : "linear-gradient(to right, #a9b2e4, #2da881)"}}>{video && video.username ? video.username[0] : null}</div>
                <div className={style.userFacts}>
                    <div className={style.name} style={{color : checked ? "#50ff93" : "#e22020"}}>{video.username}</div>
                    <div className={style.joinDate} style={{color : checked ? "#ffa0df" : "#df56e4"}}>Joined on {video.date} {video.month} , {video.year}</div>
                    <div className={style.subscriber} style={{color : checked ? "wheat" : "#9824d1"}}>{video.subscriber} {video.subscriber===0 ? 'Subscriber' : 'Subscribers'}</div>
                </div>
            </div>
            <div className={style.about} style={{border : checked ? "1px solid yellow" : "1px solid #a90489"}}>
                {video.description==='' ? 
                <div className={style.writeAbout}><textarea type="text" maxLength={1000} placeholder="Write about your channel" value={description} onChange={(e)=>setDescription(e.target.value)} style={{color : checked ? "white" : "#1da159"}} />
                <div className={style.dropdownMenu}>
                    <TiTick className={style.addIcon} onClick={()=>updateProfile({description : description})} style={{color : checked ? "yellow" : "black"}}/>
                    <div className={style.dropdownContent}>Update</div>
                </div></div> : 
                <div className={style.updatedAbout} style={{color : checked ? "white" : "#1da159"}}>{video.description}
                <div className={style.updateDescription} style={{color : checked ? "yellow" : "black"}} onClick={()=>setVideo(video =>({...video , description:''}))}>
                    <HiPencilSquare className={style.addIcon} />
                    <div className={style.updateContent}>Update</div>
                </div></div>}
            </div>
        </div>
        <div className={style.videos}>
            <p className={style.videoHeader} style={{color : checked ? "white" : "#b94c4c" , borderBottom : checked ? "2px solid white" : "2px solid #b94c4c"}}>Your Videos
                <Link to={'/UploadVideo'} style={{color : checked ? "white" : "#b94c4c" , border : checked ? "2px solid white" : "2px solid #b94c4c"}}>Upload Video</Link></p>
            <div className={style.myVideo}>
            {video.videos && video.videos.length>0 && video.videos.map(item => (
                <div className={style.item} key={item.id}>
                    <Link className={style.videoDiv} to={`/Videos/${item.id}`} style={{color : checked ? "white" : "black"}}>
                        <div>
                            <img className={style.thumbnail} src={item.imageData} alt=""/>
                            <div className={style.creator}>{item.username}</div>
                            <div className={style.title}>{item.title}</div>
                            <div className={style.views}>{item.views} {item.views>1 ? 'views' : 'view'}</div>
                        </div>
                    </Link>
                    <div>
                        <span onClick={()=>deleteVideo(item.id)} style={{color : "red" , border : "1px solid red"}}><MdDelete /> Delete</span>
                        <span onClick={()=>openModal(item.id)} style={{color : checked ? "yellow" : "blue" , border : checked ? "1px solid yellow" : "1px solid blue"}}><IoMdShare /> Share</span>
                        <Share isOpen={shareModal} onRequestClose={closeModal} contentLabel="Example Modal" link={videoLink} />
                    </div>
                </div>
            ))}
            </div>
        </div>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </div>
};
