import style from "./style.module.css";
import React from "react";

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function JobFilters(props)
{
    const { filters , handleFilter , applyFilter } = props;

    return <>
        <ReactModal
            isOpen={props.isOpen}
            onRequestClose={props.onRequestClose}
            contentLabel={props.contentLabel}
            className={style.modalContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <form className={style.form} onSubmit={applyFilter}>
                <button onClick={props.onRequestClose} className={style.close}>X</button>
                <div className={style.filterHead}>Select your Filters</div>
                <div className={style.inputContainer}>
                    <label htmlFor="title">Job Title</label>
                    <input type="text" name="title" id="title" placeholder="Enter job title" autoComplete='off' onChange={handleFilter} />
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="type">Job type</label>
                    <select id="type" name="jobType" onChange={handleFilter} >
                        <option value='' hidden>Choose Job Type</option>
                        <option value="fullTime">Full time</option>
                        <option value="partTime">Part time</option>
                    </select>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="salary">Minimum Salary</label>
                    <input type="number" name="salary" id="salary" placeholder="Enter payment (in &#8377;)" autoComplete='off' onChange={handleFilter} />
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="experience">Maximum Past Experience</label>
                    <input type="number" name="experience" id="experience" placeholder="Past Experience required (years)" autoComplete='off' onChange={handleFilter} />
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="mode">Work mode</label>
                    <select id="mode" name="workMode" onChange={handleFilter} >
                        <option value='#' hidden>Choose Work Mode</option>
                        <option value="online">Online</option>
                        <option value="offline">Offline</option>
                        <option value="hybrid">Hybrid</option>
                    </select>
                </div>
                {filters && filters.workMode==="online" ? null : <div className={style.inputContainer}>
                    <label htmlFor="location">Location</label>
                    <input type="text" name="location" id="location" placeholder="Enter location" autoComplete='off' onChange={handleFilter} />
                </div>}
                <div className={style.btnContainer}>
                    <button type='submit'>Apply</button>
                    <button type="reset">Reset</button>
                </div>
            </form>
        </ReactModal>
    </>
}
